.sectionInput{
    background-color:#999;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 45vh;
}
.job{
    width: 70%;
    height: 55%;
    border: 1px solid;
}
.section4 span{
    text-align: center;
    display: block;
}
.btn{
    display: block;
    margin: 0 auto;
    margin-top: 15px;
    width: 342px;
    height: 55px;
    background: #ffd429;
    padding: 20px 0;
    text-align: center;
    border: 1px solid #bbbbbb;
    text-decoration: none;
    color: black;
}
@media screen and (max-width: 576px) {
    .section4{
        background-color:#999;
        display: flex;
        justify-content: center;
        align-items: center;
        height: 45vh;
    }
    .job{
        width: 100%;
        height: 100%;
    }
    .section4 span{
        text-align: center;
        display: block;
    }
    .btn{
        display: block;
        margin: 0 auto;
        margin-top: 68px;
        width: 231px;
        height: 55px;
        background: #ffd429;
        padding: 20px 0;
        text-align: center;
        border: 1px solid #bbbbbb;
        text-decoration: none;
        border-radius: 10px;
    }
    .name {
        margin: 0 auto;
        display: block;
        margin-top: 65px;
        width: 275px;
        height: 48px;
        border: 1px solid #bbbbbb;
    }
    .email{
        margin: 0 auto;
        display: block;
        margin-top: 15px;
        width: 255px;
        height: 48px;
        padding-left: 22px;
        border: 1px solid #bbbbbb;
    }
    .tel{
        margin: 0 auto;
        display: block;
        margin-top: 15px;
        width: 256px;
        height: 48px;
        padding-left: 22px;
        border: 1px solid #bbbbbb;
    }
}
