.footer{
    background-color:#999;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 45vh;
}
.footer_socials{
    display: flex;
    width: 70%;
    height: 55%;
    border: 1px solid;
    justify-content: space-between;
    background-color:#ff99ff;
}
.social{
    height: 100%;
    width: 100px;
    border: 1px solid;
    background-color: #999;
}
@media screen and (max-width: 576px) {
.footer_socials{
    height: 20vh;

       }
}