.sectionJobOffer{
    background-color:#9933ff;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 95vh;
}
.mail{
    background: #ecf3f8;
    padding-top: 95px;
    padding-bottom: 100px;
}
