.header{
    display: flex;
    justify-content: flex-end;
    height: 70px;
    background-color: #ff9999;
    align-items: center;
}
.nav{
    display: flex;
    justify-content: space-between;
    align-items: center;
    display: flex;
    width: 350px;
    margin-right: 20px;
}
@media screen and (max-width: 576px) {

    .header {
        display: flex;
        height: 70px;
        background-color: #ff9999;
        align-items: center;
        justify-content: center;
    }

    .nav {
        width: 265px;
        margin: 0 auto;
    }
}
